h1 {
    font-size: 10em;

}

header {
    position: fixed;
    height: 50px;
    width: 100%;
    left: 0;
    top: 0;
    mix-blend-mode: difference;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    z-index: 999;
}

.leftLinks, .rightLinks{
    width:50%;
    height: auto;
    margin: 0 50px;
    display:-ms-inline-flexbox;
    display:inline-flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    
}


.navLinks{
    color: #ffffff;
    display: inline;
    min-height: 10px;
    padding: 20px 10px;
    text-transform: uppercase;
    font-size: .7em;
    font-weight: bold;
    cursor: pointer;

}

.viewFinder {
    overflow:hidden;
    position: relative;
}
.mapHolder{
    position: absolute;
    -webkit-transform-origin: 0% 0%;
        -ms-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
    -webkit-transform: scale(.95);
        -ms-transform: scale(.95);
            transform: scale(.95);
    -webkit-transition: all .75s;
    -o-transition: all .75s;
    transition: all .75s;

}

.mapBlocks{
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-pack: center;
        justify-content: center;
}


.map2,.map6{
    background-color: #ffffff;
}
.map7, .map8 {

    background-color: #ff0000;
}


